<style>
	.editpanel-bar {
		display: flex;
		justify-content: space-between;
		align-content: center;
		height:40px;
		border:1px solid #D0D0D0;
	}
	.editpanel-bar .material-icons {
		color:#989898;
	}
	.editpanel__item {
		display: flex;
		align-content: center;
		-webkit-user-select: none;
		   -moz-user-select: none;
		    -ms-user-select: none;
		        user-select: none;
		margin-bottom: -1px;
	}
	.editpanel__titlename {
		line-height: 22px;
		padding: 8px 8px 8px 0;
	}
	.editpanel__dragicon div {
		display: flex;
	}
	.editpanel__dragicon span {
		background: #989898;
    width: 3px;
    height: 3px;
    margin: 2px;
	}
	.editpanel__title .material-icons {
		padding: 8px;
	}
	.editpanel__drag {
		padding: 13px;
		cursor: -webkit-grab;
		cursor: grab;
	}
	.editpanel__toolsitem {
    border-left: 1px solid #D0D0D0;
	}
	.editpanel__toolsitem--active {
    background:#f7fbff;
	}
	.editpanel__toolsitem--active .material-icons,
	.editpanel__toolsitem:hover .material-icons {
    color:#0076FF;
	}
	.editpanel__toolsitem .material-icons {
		padding: 8px;
	}
	.editpanel__toolsitem:hover {
		color:#0076FF;
	}
	.editpanel-inner {
		border:1px solid #D0D0D0;
		border-top:0;
		padding:20px;
		background:#f7fbff;
	}</style>

<div class="editpanel-outer">
	<div class="editpanel-bar">
		<div class="editpanel__item editpanel__title">
			<span class="material-icons">view_quilt</span>
			<span class="editpanel__titlename">Layout</span>
		</div>
		<div class="editpanel__item editpanel__drag">
			<div class="editpanel__dragicon">
					<div>
						<span></span>
						<span></span>
						<span></span>
						<span></span>
						<span></span>
					</div>
				<div>
						<span></span>
						<span></span>
						<span></span>
						<span></span>
						<span></span>
					</div>
			</div>
		</div>
		<div class="editpanel__item editpanel__tools">
			<div class="editpanel__toolsitem editpanel__toolsitem--active">
				<a href="/">
					<span class="material-icons">border_clear</span>
				</a>
			</div>
			<div class="editpanel__toolsitem">
				<a href="/">
					<span class="material-icons">delete</span>
				</a>
