			<div class="editpanel__toolsitem">
				<a href="/">
					<span class="material-icons">arrow_drop_down_circle</span>
				</a>
			</div>
		</div>
	</div>
	<div class="editpanel-inner">
		<slot></slot>
	</div>
</div>