<style>
	.select-layout-outer {
		display: flex;
		justify-content: space-between;
		align-content: center;
	}
	.select-layout {
		display:flex;
		align-items: center;
    justify-content: center;
		background:#F9F9F9;
		flex:1;
		padding:30px;
		border:1px dotted #D0D0D0;
		height:120px;
		position: relative;
		box-shadow: inset 0 0 0px 15px white;
		-webkit-user-select: none;
		   -moz-user-select: none;
		    -ms-user-select: none;
		        user-select: none;
		border-radius: 10px;
	}
	.select-layout:hover {
		background:#EDF5FF;
		border:1px solid #0076FF;
	}
	.select-layout span {
		z-index:1;
	}
	.select-layout--two {
		margin:0 20px;
	}
	.select-layout--two::before {
    content: '';
    width: 10px;
    background: white;
    position: absolute;
    top: 0;
    bottom: 0;
	}
	.select-layout--three::before {
    content: '';
    width: 10px;
    background: white;
    position: absolute;
    top: 0;
    bottom: 0;
		left: 33%;
	}
	.select-layout--three::after {
    content: '';
    width: 10px;
    background: white;
    position: absolute;
    top: 0;
    bottom: 0;
		right: 33%;
	}</style>

<script>
	import { layoutoption } from '../stores.js';
</script>


{#if $layoutoption == 0}
<div class="select-layout-outer">
	<div class="select-layout select-layout--one" on:click={layoutoption.onecolumn}>
		<span>1 Column</span>
	</div>
	<div class="select-layout select-layout--two" on:click={layoutoption.twocolumn}>
		<span>2 Column</span>
	</div>
	<div class="select-layout select-layout--three" on:click={layoutoption.threecolumn}>
		<span>3 Column</span>
	</div>
</div>
{/if}

{#if $layoutoption != 0}
<div>
	<h1 on:click={layoutoption.backbutton}>
		Back
	</h1>

	{#if $layoutoption == 1}
	<div>
		1 column
	</div>
	{/if}

	{#if $layoutoption == 2}
	<div>
		2 column
	</div>
	{/if}

	{#if $layoutoption == 3}
	<div>
		3 column
	</div>
	{/if}
</div>
{/if}